/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
  height: 100vh;
  display: flex;
  flex-direction: column; /* Stack header and container */
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease, color 0.3s ease;
}

:root {
  /* Light Mode */
  --background-color: #d6d6d6; /* Slightly darker grey for light mode */
  --text-color: #333333; /* Darker text for contrast */
  --container-background: #f0f0f0; /* Slightly off-white container */
  --button-primary: #007bff; /* Bright blue for buttons */
  --button-primary-hover: #0056b3;
  --button-border: #0056b3; /* Button border for better styling */
  --button-text: #ffffff; /* White text inside buttons */
  --button-secondary: #28a745; /* Bright green for Upload button */
  --button-secondary-hover: #218838;
  --metadata-background: #e6e6e6; /* Lighter grey for metadata */
  --metadata-border: #cccccc; /* Light grey border */
}

/* Dark Mode */
body.dark-mode {
  --background-color: #121212; /* Black background for dark mode */
  --text-color: #e0e0e0; /* Light text for dark mode */
  --container-background: #1f1f1f; /* Slightly lighter black box */
  --button-primary: #7289da; /* Muted blue */
  --button-primary-hover: #5865f2;
  --button-border: #5865f2;
  --button-text: #ffffff;
  --button-secondary: #43b581; /* Muted green */
  --button-secondary-hover: #3ba374;
  --metadata-background: #292929;
  --metadata-border: #444444;
}

/* Header Styling */
h1 {
  font-size: 2.8rem;
  color: var(--text-color);
  margin: 0 0 20px 0; /* Add spacing below header */
  text-align: center;
}

/* App Container */
.container {
  width: 100%;
  max-width: 800px; /* Adjusted size for box */
  background: var(--container-background);
  padding: 40px 30px;
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1); /* Softer shadow for light mode */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align content to the left */
  gap: 20px; /* Add spacing between child elements */
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Inner Title Styling ("Devin") */
.header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; /* Make header row fill the container width */
}

h2 {
  font-size: 1.8rem;
  color: var(--text-color);
  margin: 0; /* Remove default margin */
}

/* Toggle Container */
.toggle-container {
  display: flex;
  align-items: center;
  font-size: 1rem;
  gap: 5px;
}

/* File Upload Styling */
.custom-file-upload {
  position: relative;
  display: inline-block;
  padding: 15px 30px;
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--button-text);
  background-color: var(--button-primary);
  border: 2px solid var(--button-border);
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  overflow: hidden;
}

.custom-file-upload:hover {
  background-color: var(--button-primary-hover);
  transform: scale(1.05); /* Add subtle hover animation */
}

.custom-file-upload input {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.file-name {
  margin: 15px 0;
  font-size: 1rem;
  color: var(--text-color);
  text-align: left;
}

/* Button Styling */
button {
  background-color: var(--button-secondary);
  color: white;
  border: none;
  padding: 15px 25px;
  margin-top: 20px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.3s ease, transform 0.2s;
  width: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

button:hover {
  background-color: var(--button-secondary-hover);
  transform: scale(1.05);
}
